@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.flx,
.present-slider-item,
.header-cont-cards,
.header,
.ftr,
.main-works,
.main {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.flx-col,
.author-feed form div,
.author-feed form {
  display: flex;
  flex-direction: column;
}

.wh,
.main-pers-cont-inf {
  width: 100%;
  height: auto;
}

.f-cent,
.header,
.ftr {
  justify-content: center;
  align-items: center;
}

.posit-rel,
.present-slider-item-block2,
.present-slider-item-block,
.present-slider,
.header-cont-cards div,
.header-cont,
.header-title,
.card-image img,
.main-pers-cont-img {
  position: relative;
  width: 100%;
}

.img-bg-pos,
.present-slider-item-block img,
.present-slider-btn img,
.card-image img {
  background-position: center center;
  background-size: cover;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

body {
  font-family: NSARegular, "Roboto", sans-serif;
  overflow-x: hidden;
  background-color: #F8F2E4;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0px 15px;
}

button {
  border: none;
  background: transparent;
}

/*Modals*/

.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
  background-image: linear-gradient(75deg, rgba(210, 0, 17, 0.7) -5%, rgba(0, 49, 155, 0.7) 60%, rgb(234, 163, 0) 90%);
}

/*Our works*/

.works {
  padding: 5rem 0;
}

.works img {
  margin-bottom: 1rem;
}

a:focus {
  outline: none;
}

.bigImg {
  width: 500px;
  background-position: center center;
  background-size: cover;
  box-shadow: 10px -20px;
}

header {
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center center;
  background-attachment: fixed;
}

a {
  text-decoration: none;
  color: none;
}

.contr {
  max-width: 1200px;
  margin: 10px auto;
}

#present {
  background: linear-gradient(to top, transparent, rgba(2, 35, 106, 0.7)) no-repeat bottom;
  padding: 2rem 0;
}

.animated {
  animation: fadeInRight 1s ease-in-out;
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(300px);
  }

  to {
    opacity: 1;
  }
}

.col-4 {
  position: relative;
  width: 100%;
}

.col-8 {
  position: relative;
  width: 100%;
}

.i {
  color: white;
  font-size: 20px;
}

.menu-toggle {
  width: 65px;
  height: auto;
  float: left;
  margin-left: 30px;
  margin-top: 10px;
  overflow: hidden;
  padding: 4px;
}

.menu-toggle span {
  display: block;
  height: 4px;
  margin: 6px;
  width: 95%;
  background-color: rgb(7, 4, 4);
  border-radius: 20%;
  box-shadow: 0 1px 1px #FFFFFF;
}

.txtMyu {
  color: #FFFFFF;
  font-style: normal;
  letter-spacing: 1px;
  font-weight: 600;
  font-size: 22px;
  margin-left: 120px;
  margin-top: 10px;
  overflow: hidden;
  font-family: "Marck Script", cursive;
  text-shadow: 0px 0px 0 rgb(0, 0, 0), 0px 1px 0 rgb(0, 0, 0), 0px 2px 1px rgba(0, 0, 0, 0.01), 0px 2px 2px rgba(0, 0, 0, 0.5), 0px 0px 2px rgba(0, 0, 0, 0.2);
}

.nav {
  width: 250px;
  height: 100%;
  overflow: hidden;
  transition: all 1s linear;
  position: absolute;
  top: 36px;
  left: -270px;
  bottom: 0;
  z-index: 999;
  background-color: rgb(0, 0, 0);
}

.nav-search {
  box-shadow: 0 0 5px #508878, 3px 3px 5px 0 #ccc;
  margin-left: -10px;
  border-radius: 3px;
  padding: 2px 2px;
}

.nav-search input {
  height: 30px;
  padding: 5px;
  width: 80%;
  border: none;
  position: relative;
}

.nav-search input:focus {
  outline: 0;
  outline-offset: 0;
}

.mobile {
  transform: translate(100%);
  list-style: none;
  text-decoration: none;
  position: fixed;
  line-height: 2;
  top: 56px;
  transition: all 1s linear;
}

.nav ul {
  margin-top: 30px;
}

.nav ul li {
  margin-left: 30%;
}

.nav ul li a {
  width: 153px;
  margin: 5px;
  color: #FFFFFF;
  font-style: normal;
  letter-spacing: 1px;
  font-weight: 600;
  font-size: 20px;
  list-style: none;
  display: flex;
  letter-spacing: 1px;
  text-shadow: 1px 1px 1px hsla(0deg, 0%, 100%, 0.6);
}

nav {
  width: 250px;
  height: 50px;
  backdrop-filter: blur(0.85rem);
  box-shadow: 0 0 10px;
  background-color: rgba(51, 45, 65, 0.062745098);
  position: fixed;
  top: 0;
  left: 0;
  text-align: left;
  z-index: 9999;
  overflow: hidden;
}

.main {
  max-width: 1100px;
  height: auto;
  backdrop-filter: blur(0.65rem);
  box-shadow: 0 0 10px;
  background-color: rgba(51, 45, 65, 0.062745098);
  margin: 30px auto;
  justify-content: center;
  align-items: center;
}

.main-pers {
  position: relative;
  flex: 0 0 100%;
}

.main-pers-cont {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.main-pers-cont-inf {
  position: relative;
}

.main-pers-cont-inf p {
  text-align: center;
  margin-left: 12px;
}

.main-pers-cont-inf p span {
  display: block;
  text-align: left;
  line-height: 2;
  color: #0F002A;
  font-style: normal;
  letter-spacing: 1px;
  font-weight: 700;
  font-size: 15px;
}

.main-pers-cont-inf p span:first-of-type {
  color: #0F002A;
  font-style: normal;
  letter-spacing: 1px;
  font-weight: 500;
  font-size: 25px;
  margin-bottom: 10px;
}

.main-pers-cont-inf p span:first-of-type:hover {
  text-decoration: underline;
  cursor: pointer;
}

.main-pers-cont-img {
  min-height: 50vh;
}

.main-pers-cont-img img {
  width: 220px;
  height: 190px;
  margin-top: 20px;
  position: absolute;
  object-fit: scale-down;
  top: 5px;
  left: 30px;
  border-radius: 20px;
}

.main-pers-cont-img img:hover {
  cursor: pointer;
  box-shadow: 0px 0px 30px -4px rgba(73, 71, 71, 0.6);
}

.main-pers-cont-img::before {
  content: " ";
  background-size: contain;
  background-repeat: no-repeat;
  width: 260px;
  height: 260px;
  position: absolute;
  top: 5px;
  left: 20px;
  border-right: dashed 10px #5E4A3F;
  border-bottom: dashed 3px #5E4A3F;
  border-radius: 20%;
}

.main-works {
  justify-content: center;
  align-items: center;
}

.main-works a {
  position: relative;
  margin: 0 3px;
}

.main-works a:hover {
  transition: all 200ms ease-in;
  transform: scale(1.1);
  z-index: 22;
}

.main-works a img {
  width: 290px;
  height: 250px;
  background-repeat: no-repeat;
  background-position: center center;
  object-fit: cover;
  box-shadow: 0 0 10px;
}

.author {
  padding: 5px;
}

.author-inf {
  margin: 20px auto;
  max-width: 1100px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.author-inf-img {
  width: 60%;
}

.author-inf-img img {
  width: 100%;
  height: auto;
  background-position: center center;
  object-fit: fill;
}

.author-works {
  max-width: 1100px;
  margin: 10px auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.author-feed {
  max-width: 1000px;
  margin: 20px auto;
  position: relative;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}

.author-feed form {
  width: 100%;
  height: auto;
  background-color: #FFFFFF;
  padding: 20px;
}

.author-feed form div {
  margin: 20px;
}

.author-feed form textarea {
  min-height: 170px;
  resize: none;
  max-width: 90vw;
  padding: 20px;
}

.author-feed form input {
  display: block;
  max-width: 400px;
  height: 40px;
  margin-top: 30px;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid #897042;
  border-radius: 15px;
  padding: 5px;
  color: black;
  font-style: normal;
  letter-spacing: 1px;
  font-weight: 700;
  font-size: 17px;
}

.author-feed form select {
  position: relative;
  outline: 0;
  border: 0;
  border-top: 2px solid #897042;
  border-bottom: 2px solid #897042;
  border-radius: 14px;
  padding: 0 1em;
  color: rgb(12, 12, 12);
  background-color: transparent;
  cursor: pointer;
  display: flex;
  width: 20em;
  height: 3em;
  border-radius: 0.25em;
  overflow: hidden;
}

.author-feed form select option {
  margin: 15px 0;
  border-bottom: 1px solid #cbad75;
}

.author-feed form-select option {
  margin: 15px 0;
  border-bottom: 1px solid #cbad75;
}

.author-feed form button {
  margin-top: 60px;
  width: 150px;
  height: 40px;
  background-color: transparent;
  border: 2px solid #897042;
  color: black;
  cursor: pointer;
}

.author-feed form button:hover {
  box-shadow: 0 0 10px #c78030;
}

.card-title {
  color: #fff;
  font-style: normal;
  letter-spacing: 1px;
  font-weight: 700;
  font-size: 17px;
  position: absolute;
  bottom: 0;
  left: 0;
  max-width: 100%;
  padding: 24px;
}

.picture-card {
  max-width: 300px;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card {
  margin: 20px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}

.card-image {
  position: relative;
  width: 90%;
  height: auto;
}

.card-image img {
  object-fit: cover;
}

.card-image img {
  display: block;
  border-radius: 2px 2px 0 0;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.picture-card {
  max-width: 300px;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card {
  position: relative;
  margin: 0.5rem 0 1rem 0;
  background-color: #fff;
  transition: box-shadow 0.25s;
  border-radius: 2px;
}

.card-content {
  padding: 24px;
  border-radius: 0 0 2px 2px;
}

.row {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

.chip {
  display: inline-block;
  height: 32px;
  font-size: 13px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.6);
  line-height: 32px;
  padding: 0 12px;
  border-radius: 16px;
  background-color: #e4e4e4;
  margin-bottom: 5px;
  margin-right: 5px;
}

strong {
  font-weight: 500;
}

.price {
  width: 70px;
  height: 70px;
  font-weight: 900;
  font-size: 1.45rem;
  line-height: 70px;
  margin: 10px;
  position: absolute;
  top: 0;
  letter-spacing: 1.4px;
}

.clr {
  background-color: #897042;
}

.card-action-buttons {
  margin: -200px 10px 0 0;
  text-align: right;
}

.card-action-buttons ul {
  list-style-type: none;
}

.card-action-buttons ul li {
  display: inline-flex;
  padding-left: 7px;
}

.btn {
  color: white;
  border: none;
  border-radius: 2px;
  display: inline-block;
  height: 36px;
  line-height: 36px;
  padding: 0 2rem;
  text-transform: uppercase;
  vertical-align: middle;
}

.btn-fl {
  position: relative;
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  width: 40px;
  height: 40px;
  padding: 0;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  z-index: 1;
  background-color: #fff;
  outline: 0;
  border-radius: 50%;
  text-align: center;
  color: black;
}

.btn-fl svg {
  margin-top: 2px;
  width: 33px;
  height: 33px;
  padding: 2px;
}

.ftr {
  height: 90px;
  padding: 20px;
  background-color: #000000;
  overflow: hidden;
  padding-bottom: 20px;
  border-top: 16px solid #4b4a4a;
  color: #4b4a4a;
  font-style: normal;
  letter-spacing: 1px;
  font-weight: 400;
  font-size: 17px;
}

.footer-name a {
  color: white;
}

.header {
  margin-top: 0;
  width: 100%;
  min-height: 550px;
  background-image: linear-gradient(75deg, rgba(210, 0, 17, 0.7) -5%, rgba(0, 49, 155, 0.7) 60%, rgba(234, 163, 0, 0.6) 90%);
}

.header-title {
  letter-spacing: 1.4px;
  font-family: "Uchen", serif;
  text-align: center;
  color: wheat;
  font-style: normal;
  letter-spacing: 1px;
  font-weight: 700;
  font-size: 45px;
  text-shadow: 0px 0px 0 rgb(0, 0, 0), 0px 1px 0 rgb(0, 0, 0), 0px 2px 1px rgba(24, 23, 23, 0.2), 0px 2px 2px rgba(59, 59, 60, 0.2), 0px 0px 2px rgba(80, 80, 78, 0.2);
}

.header-cont-cards {
  align-items: baseline;
}

.header-cont-cards div {
  margin: 6px 0;
}

.header-cont-cards_item {
  width: 100%;
  background-color: #FFFFFF;
  box-shadow: 0 0 10px;
}

.header-cont-cards_item img {
  width: 100%;
  height: 250px;
  background-position: center center;
  background-size: cover;
}

.header-cont-cards_item span {
  display: block;
  margin-top: 5px;
  text-align: left;
  padding: 10px;
  letter-spacing: 1px;
  color: black;
  font-style: normal;
  letter-spacing: 1px;
  font-weight: 600;
  font-size: 17px;
  text-shadow: 0px 0px 0 rgb(0, 0, 0), 0px 1px 0 rgb(0, 0, 0), 0px 2px 1px rgba(236, 23, 23, 0.2), 0px 2px 2px rgba(42, 10, 117, 0.2), 0px 0px 2px rgba(237, 237, 17, 0.2);
}

.present-slider {
  height: auto;
}

.present-slider-btn {
  margin-top: 10px;
  z-index: 2;
  display: inline-flex;
}

.present-slider-btn img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  height: auto;
  box-shadow: 0 0 10px;
  margin-right: 30px;
}

.present-slider-btn:hover {
  cursor: pointer;
}

.present-slider-item {
  justify-content: center;
}

.present-slider-item-block img {
  max-width: 260px;
  min-height: 250px;
  box-shadow: 0 0 10px;
}

.present-slider-item-block2 {
  text-align: left;
}

.present-slider-item-heading {
  margin-top: 4rem;
  font-size: 22px;
  font-family: "Circe Extra Bold", sans-serif;
  line-height: 1.1;
}

.present-slider-item-text {
  margin: 3rem 0;
  font-size: 20px;
}

