  font-family: NSARegular;
  src: url("../fonts/NotoSansArmenian-Regular.ttf");
}

@font-face {
  font-family: NSAMedium;
  src: url("../fonts/NotoSansArmenian-Medium.ttf");
}

@font-face {
  font-family: NSALight;
  src: url("../fonts/NotoSansArmenian-Light.ttf");
