@media (min-width: 992px) {
  .col-4 {
    flex: 0 0 30%;
    max-width: 30%;
  }

  .col-8 {
    flex: 0 0 70%;
    max-width: 70%;
  }

  .header-title {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .header-cont {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .header-cont-cards div {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .present-slider-item-block {
    flex: 0 0 30%;
    max-width: 30%;
  }

  .present-slider-item-block2 {
    flex: 0 0 70%;
    max-width: 70%;
  }

  .col-4 {
    flex: 0 0 30%;
    max-width: 30%;
  }

  .col-8 {
    flex: 0 0 70%;
    max-width: 70%;
  }
}

@media screen and (max-width: 1680px) {
  header {
    background-size: cover;
  }

  header {
    background-size: cover;
  }
}

@media (max-width: 992px) {
  .author-inf {
    padding: 10px;
  }

  .author-works {
    padding: 10px;
  }

  .header-cont {
    flex: 0 0 100%;
    max-width: 100%;
  }

  .header-cont-cards div {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

@media (max-width: 692px) {
  .main-pers-cont {
    flex-wrap: wrap;
    margin: 15px 10px;
  }

  .main-works {
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 500px) {
  .header-title {
    flex: 0 0 100%;
    max-width: 100%;
    font-size: 35px;
    margin-top: 80px;
  }

  .header-cont-cards div {
    flex: 0 0 100%;
    max-width: 100%;
    padding: 10px;
  }

  .header-cont-cards_item {
    width: 90%;
  }

  .header-cont-cards_item img {
    object-fit: cover;
  }

  .present {
    padding: 8px;
  }
}

@media (max-width: 392px) {
  .main-works a img {
    width: 80%;
    height: 200px;
    background-size: cover;
  }
}